.editorWrapper {
  position: relative;
  height: 100%;
  width: 100%;
}

.formatterButton {
  position: absolute !important;
  top: 0.5rem;
  right: 1.5rem;
  z-index: 100;
  opacity: 0;
  border-radius: 50%;
  padding: 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #666;
  cursor: pointer;
  transition: opacity 0.3s ease-in-out !important;
}

.editorWrapper:hover .formatterButton {
  opacity: 1;
}
